html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
  background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

.App {
  text-align: center;
  min-height: 100%;
}

.particles {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.fw600 {
  font-weight: 600;
}

.center {
  display: flex;
  justify-content: center;
}

.tilt {
  background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
  background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%);
  height: 125px;
  width: 125px;
}

.tilt-inner {
  height: 100%;
}

@media (min-width: 350px) {
  .tilt {
    height: 150px;
    width: 150px;
  }
}

.bounding-box {
  position: absolute;
  box-shadow: inset 0 0 0 3px #149df2;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.form {
  width: 700px;
  background-color: silver;
  background-image: -webkit-radial-gradient(
      100% 150%,
      circle,
      silver 24%,
      white 25%,
      white 28%,
      silver 29%,
      silver 36%,
      white 36%,
      white 40%,
      transparent 40%,
      transparent
