    -webkit-radial-gradient(
      50% 100%,
      circle,
      white 10%,
      silver 11%,
      silver 23%,
      white 24%,
      white 30%,
      silver 31%,
      silver 43%,
      white 44%,
      white 50%,
      silver 51%,
      silver 63%,
      white 64%,
      white 71%,
      transparent 71%,
      transparent
    ),
    -webkit-radial-gradient(
      100% 50%,
      circle,
      white 5%,
      silver 6%,
      silver 15%,
      white 16%,
      white 20%,
      silver 21%,
      silver 30%,
      white 31%,
      white 35%,
      silver 36%,
      silver 45%,
      white 46%,
      white 49%,
      transparent 50%,
      transparent
    ),
    -webkit-radial-gradient(
      0 50%,
      circle,
